<template>
    <div>
        <!-- 구글로그인 : loginvue24 -->
        <!-- 로그인 버튼 -->
        <button v-if="!isLoggedIn" @click="login" class="google-button">
            <img src="@/assets/google-login.png" alt="Login with Google" class="google-button-img" />
        </button>
        <!-- 로그아웃 버튼 -->
        <button v-if="isLoggedIn" @click="logout" class="logout-button">Logout</button>
    </div>
</template>

<script setup>
import { ref } from 'vue'; 


const oauthClientId = process.env.VUE_APP_OAUTH_CLIENT;

// 로그인 상태를 관리하는 반응형 변수
const isLoggedIn = ref(false); //초기 로그아웃상태여야함


const login = () => {
    console.log('=== 구글 로그인 시도 ===');
    console.log('클라이언트 ID:', oauthClientId);

    if (!oauthClientId) {
        alert('OAuth 클라이언트 ID가 설정되지 않았습니다.');
        return;
    }

    // Google Identity Services가 로드될 때까지 대기
    const checkGoogleLoaded = () => {
        if (window.google && window.google.accounts && window.google.accounts.oauth2) {
            console.log('Google Identity Services 로드 완료');
            
            // OAuth 2.0 클라이언트 초기화
            const client = window.google.accounts.oauth2.initCodeClient({
                client_id: oauthClientId,
                scope: 'email profile openid',
                ux_mode: 'popup',
                callback: (response) => {
                    console.log('구글 로그인 응답:', response);
                    if (response.code) {
                        console.log('로그인 성공! 코드:', response.code);
                        isLoggedIn.value = true;
                        alert('로그인 성공!');
                    } else if (response.error) {
                        console.error('로그인 오류:', response.error);
                        alert('로그인에 실패했습니다: ' + response.error);
                    }
                }
            });

            console.log('OAuth 클라이언트 초기화 완료');
            client.requestCode();
        } else {
            console.log('Google Identity Services 로드 대기 중...');
            setTimeout(checkGoogleLoaded, 100);
        }
    };

    checkGoogleLoaded();
};

/**
 * 로그아웃 처리
 * 로그인 상태를 초기화하고, 세션 정리 등 추가 작업을 수행합니다.
 */
const logout = () => {
    
    console.log('User logged out');
    isLoggedIn.value = false;
};
</script>
<style scoped>
.google-button {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
}

.google-button-img {
    width: 200px; /* 버튼 이미지 크기 */
    height: auto;
    display: block;
}

.google-button:hover {
    opacity: 0.9; /* 마우스 오버 시 효과 */
}

.logout-button {
    padding: 10px 20px;
    background-color: #4285f4;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
}

.logout-button:hover {
    background-color: #357ae8; /* 마우스 오버 시 색상 변경 */
}
</style>
